import React, { Fragment } from "react";

import forPocProofImg from "../../styles/assets/images/PocDevlopment/proof.jpg";
import forPocResearchImg from "../../styles/assets/images/PocDevlopment/research-con.webp";
import forPocDevelopmentImg from "../../styles/assets/images/PocDevlopment/developmentcon.jpg";
import { Carousel } from "react-bootstrap";

const DevelopmentServices = () => {
  return (
    <Fragment>
      <div className="business-wrap my-5 pt-5">
        <div className="container-fluid">
          <div className="business-need p-5 row">
            <div className="col-12">
              <div className="mx-5">
                <h3 className="mb-2 fw-bold">
                  POC And Open Source Development Services
                </h3>
                <p className="text-dark fw-semibold fs-3 mt-4">
                  Integrating Proof of Concept (POC) and Open Source Development
                  Services into our company's offerings is a strategic decision
                  that can significantly enhance our competitiveness and value
                  proposition.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <main>
        <div className="container merncarousel">
          <div className="business">
            <div className="text-center justify-content-center d-flex row">
              <div className="col-md-10 pb-5">
                <h4 className="fw-semibold text-dark my-5">
                  Why should you invest in a professional web development team
                  for your business? Here are just a few reasons
                </h4>
              </div>
            </div>
          </div>

          <div className="d-flex align-items-center row mb-5">
            <div className="col-md-12">
              <Carousel interval={100000}>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="fs-5 fw-semibold p-5">
                        <p className="fs-4 fw-semibold">
                          With years of experience working with a wide range
                          ofclients and technologies, we are well-equipped to
                          help you unlock the full potential of your POC and
                          open source projects. Whether you're looking to create
                          a new product or service, or simply need help
                          optimizing an existing one, we are here to help.
                        </p>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={forPocProofImg}
                        alt="poc-proof-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="mern-content">
                        <p className="fs-4 fw-semibold">
                          Our POC and open source development services
                          includeeverything from research and concept
                          development, to prototyping and testing, to full-scale
                          development and deployment. We work closely with you
                          to understand your unique needs and goals, and then
                          use our expertise to help you achieve them.
                        </p>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={forPocResearchImg}
                        alt="research-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="mern-content">
                        <p className="fs-4 fw-semibold">
                          With our cutting-edge technologies and proven
                          development methodologies, we can help you create
                          truly innovative and flexible products and services
                          that are sure to stand out in the marketplace. And
                          with our commitment to open source development, you
                          can be confident that your projects will be built on
                          the latest and greatest technologies, without the need
                          to spend a fortune on licensing fees.
                        </p>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={forPocDevelopmentImg}
                        alt="poc-development-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
              </Carousel>
            </div>
          </div>
        </div>
      </main>
    </Fragment>
  );
};

export default DevelopmentServices;
