import Layout from "../components/layout";
import React from "react";
import PocOpenSourceScreen from "../screens/poc-open-source";
import Seo from "../components/seo";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import MoveForward from "../components/common/move-forward";
import BreadCrumb from "../components/common/breadcrumb";

const PocOpenSource = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "POC and Open Source Development Services", link: null },
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <PocOpenSourceScreen />
      <MoveForward db={db} />
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="POC Open-Source Development Services"
    description="Explore open-source possibilities with Justtry Technologies. We specialize in POC development, delivering customized solutions with open-source technologies. Elevate your projects with innovation and reliability. Discover more with us.
    "
    keywords="Proof of Concept (POC) Development, Open-Source POC Solutions,Prototype Development Services, POC Software Development, POC Application Design, Open-Source Technology Stack, POC Customization Services, POC Project Consulting, Rapid Prototyping, POC MVP (Minimum Viable Product) Development, POC Testing and Validation, POC Codebase Review, POC Open-Source Integration, Open-Source POC Frameworks, POC UI/UX Design, Open-Source POC Deployment, POC Scalability Assessment, POC Security Analysis, POC Documentation Services, POC Maintenance and Support, Open-Source Community Collaboration, POC Code Refactoring, Cross-Platform POC Development, POC for Emerging Technologies, POC for New Features or Modules"
  ></Seo>
);

export default PocOpenSource;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
